@import url(https://fonts.googleapis.com/css2?family=Nanum+Myeongjo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Allura&family=Nanum+Myeongjo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.top {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  align-items: center;
  background-color: #2f5241;
  /* width: 400px; */
  height: 500px;
}

.main-title {
  color: white;
  font-family: "Allura", cursive;
  font-size: 3rem;
}

.only-box {
  position: absolute;
  top: 7px;
  left: -17px;
  width: 280px;
  height: 763px;
  border: 2px solid;
  border-color: #beb24a;
  z-index: 0;
}

.squre {
  margin: 0rem auto 0;
  max-width: 310px;
}

.to-map {
  max-width: 400px;
}

.App-header {
  /* background-color: #282c34; */
  background-color: #faf7f6;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(7px + 2vmin);
  color: #2f5241;
}

.App-link {
  color: #61dafb;
}

.schedule {
  font-family: "Nanum Myeongjo", serif;
  margin-bottom: 2rem;
}

.schedule ul {
  line-height: 2rem;
  text-align: left;
}

.invite {
  margin-top: 32rem;
  max-width: 20rem;
  line-height: 1.5rem;
  font-family: "Nanum Myeongjo", serif;
  margin-bottom: 2rem;
}

.map {
  line-height: 2rem;
}

.map h2 {
  font-family: "Nanum Myeongjo", serif;
}

.to-map {
  max-width: 350px;
}

.line {
  size: 3px;
  width: 10rem;
  border-color: whtie;
}

.copyBtn {
  width: 17rem;
  height: 5rem;
  border: none;
  background-color: #2f5241;
  color: white;
  text-decoration: none;
  font-weight: bolder;
  font-size: large;
  display: block;
  text-align: center;
  margin: 3rem auto;
  top: 5rem;
}

